export const getPosts = async (category: Category = 'ALL'): Promise<Post[]> => {
    try {
        let q = query(
            collection(db, COLLECTION_NAME),
            where('status', '==', 'published'),
            orderBy('createdAt', 'desc'),
            limit(20)
        );

        if (category !== 'ALL') {
            q = query(
                collection(db, COLLECTION_NAME),
                where('status', '==', 'published'),
                where('category', '==', category),
                orderBy('createdAt', 'desc'),
                limit(20)
            );
        }

        const snapshot = await getDocs(q);

        if (snapshot.empty) {
            console.log("Firestore is empty, returning dummy data");
            // Filter dummy data
            if (category === 'ALL') return DUMMY_POSTS;
            return DUMMY_POSTS.filter(p => p.category === category);
        }

        return snapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
        } as Post));
    } catch (error) {
        console.error("Error fetching posts:", error);
        // Fallback to dummy data on error (e.g. permission denied)
        if (category === 'ALL') return DUMMY_POSTS;
        return DUMMY_POSTS.filter(p => p.category === category);
    }
};
